body {
    margin: 0;
    height: 100vh;
    /*color: "#353535";*/
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    background-color: #e1e1e1;
}

#root,
.App {
    font-family: sans-serif;
    height: 100%;
    display: flex;
    position: relative;
}

.App .main {
    padding: 24px;
    flex-grow: 1;
    display: flex;
}

.sidebar-btn-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 24px;
}

.sidebar-btn {
    padding: 1px 15px;
    border-radius: 40px;
    background: rgba(255, 255, 255, 0.05);
    color: #adadad;
    text-decoration: none;
    margin: 0 auto;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-overflow: ellipsis;
    overflow: hidden;
}

.sidebar-btn span {
    margin-left: 10px;
    font-size: 13px;
}


